/* eslint no-restricted-globals: ["off"] */
import { useParams } from "react-router-dom";
import Discount from "./Discount";
import { useEffect, useState } from "react";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import axios from "axios";
import { addCart } from "../../store";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";

function Detail(props) {
  let [alert, setAlert] = useState(true);
  const [key, setKey] = useState("home");
  const [detail, setDetail] = useState([]);
  let imsi = [];

  let dispatch = useDispatch();
  let navigate = useNavigate();

  // 2  alert state -> false
  useEffect(() => {
    let myTimer = setTimeout(() => {
      setAlert(false);
    }, 2000);
    return () => {
      clearTimeout(myTimer);
    };
  }, []);

  useEffect(()=>{
    axios
    .get('https://zzzmini.github.io/js/shoesReview.json')
    .then((result)=>{
      //    
      // console.log(result.data)
      
      imsi = [...result.data]; 
      console.log('imsi ', imsi)
      setDetail([... imsi]);
    })
    .catch(()=>{
      //    
      console.log(".")
    });
  },[ ])


  let { id } = useParams();

  let findProduct = props.product.find(function (x) {
    return x.id == id;
  });

  if (findProduct == null) {
    alert("  .");
    history.back();
    return;
  } else {
    
    let shoes = props.product[id];
    
    let i = parseInt(id) + 1;
    
    
    let searchArr = [];
    detail.forEach((x)=>{
      if(x.productId == i){
        searchArr.push(x);
      }
    })

    // setDetail([... searchArr]);

    console.log("=deta==", searchArr)
    console.log("=i==", i)

    let strPrice = shoes.price.toLocaleString("ko-KR");

    // setProductId(id);

    return (
      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <img
              src={`${process.env.PUBLIC_URL}/images/shoes${i}.jpg`}
              width={"100%"}
            />
          </div>
          {alert == true ? <Discount /> : null}
          <div className="col-md-6">
            <h4 className="pt-5">{shoes.title}</h4>
            <p>{shoes.content}</p>
            <p>{strPrice}</p>

            <button className="btn btn-danger" 
              onClick={()=>{
                dispatch(addCart(shoes))
                navigate('/cart')
            }}></button>
          </div>

          <Tabs
            defaultActiveKey="home"
            id="uncontrolled-tab-example"
            className="mb-3"
          >
            <Tab eventKey="home" title="   ">
              <div>
                <p>
                   

                  

                 :  (  )
                 : 
                   14       .
                     .

                   
                  

                 

                 :  10 30  ,   (,  )
                 :  ,  ,  
                 : 5,000

                 
                </p>
              </div>
            </Tab>
            <Tab eventKey="profile" title="(4.5 )" >
              <div>
                { searchArr.map((x)=>{
                  return(
                  <div key={x.reviewId}>
                    {x.title} <br></br>
                    {x.review}
                    <hr></hr>
                  </div>
                  )
                })
              }
              
              </div>
            </Tab>
            <Tab eventKey="contact" title=" ">
              <div>
                <p>
                  

                   :           .     ,       .
                   A/S  : ()    / 080-022-0182
                      :   ' '   .
                      :   ' '    .
                </p>
              </div>
            </Tab>
          </Tabs>
        </div>
        {/* <Row className="justify-content-md-center">
          {props.product.map((p, index) => {
            return (
              <Col>
                <Product product={product} index={index} />
              </Col>
            );
        })}
        </Row> */}
      </div>
    );
  }
}
export default Detail;
